html, body, #app, #app>div {
    height: 100%;
    width: 80%;
    background-color: #001318;
    color: white;
    margin-left: auto;
    margin-right: auto;
    font-family: Arial, Arial, Helvetica, sans-serif;
    font-size: 25px;
}

.topNavBar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 5px;
    margin-top: .5rem;
    background-color: #08242c;
    border-radius: 5px;
}

.infoContainer {
    display: flex;
    flex-direction: column;
    width: 470px;
}

.infoItems {
    text-align: left;
    background-color: transparent;
    border: none;
    resize: none;
    color: white;
}

.center {
    text-align: center;
}

.separator {
    height: 50px;
}

.rows {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.gridContainer {
    display: grid;
    grid-template-columns: 40% 40%;
    justify-content: space-around;
}

img {
    margin-top: 2rem;
    border: 4px solid black;
    border-top-left-radius: 25px;
    border-bottom-right-radius: 25px;
    width: 300px;
    height: 400px;
}


.workButton {
    cursor: pointer;
    width: 300px;
    background-color: rgb(4, 37, 65);
    color : white;
    border-radius: 8px;
}

.workButton:hover {
    background-color: rgb(17, 65, 104);
}

.navButton {
    cursor: pointer;
    height: 40px;
    width: 20%;
    background-color: #001318;
    color : white;
    border-radius: 5px;
}

.navButton:hover {
    background-color: #006443;
}

a {
    color: white
}

a:hover {
    color: rgb(0, 0, 0)
}

.iconStyle {
    width: 50px;
    height: 50px;
}

